import React,{useState} from "react";
import { HashRouter as Router,Route } from "react-router-dom";
 import Scan1 from "./Scan1";
 import Scan2 from "./Scan2";
 import Signup from "./signup";
 import Login from "./login";
 import {Header,Footer,Dropdown} from "./containers";
function App() { 
  const [isOpen, setisOpen] = useState(false);

  const toggle = ()=>{
    setisOpen(!isOpen);
  }
  return (
    <Router>
    <div className="App bg-gray-200 font-sans flex flex-col h-full sm:h-screen justify-between items-center">
      <Header toggle={toggle}/>
      <Dropdown isOpen={isOpen}/>
      <main class="mt-16 w-full flex flex-col items-center">
        <div class="max-w-6xl w-full ">
      
        <Route exact path="/">
          <Scan1/>
          </Route>
        <Route exact path="/security/signup">
          <Signup/>
        </Route>
        <Route exact path="/security/login">
          <Login/>
        </Route>
        <Route exact path="/:number">
            <Scan2/>
        </Route>
     
      </div>
      </main>
      <Footer/>
    </div>
    </Router>
  );
}
export default App;
