import Axios from "axios";
import { apiUrl,authHeader} from "../../_helps";
export const checkServices = {
    check,setEnter
}
function check(number) {
    // const requestOptions = {
    //     headers: {
    //         'Access-Control-Allow-Origin':'*'
    //     }
    // };
    return Axios.get(`${apiUrl()}/check/${number}`)
        .then((response) => {
            return response;
        });
}

function setEnter(number) {
    const data = null;
    const requestOptions = {
        headers: authHeader()
    };
    return Axios.put(`${apiUrl()}/check/${number}`,data,requestOptions)
        .then((response) => {
            return response;
        });
}