import result from 'autoprefixer/data/prefixes';
import React,{useState} from 'react';
import {usersServices} from "./_services";
import FullPageLoader from "./_components/FullPageLoader/FullPageLoader";

export default function Signup() {
    const [loading, setloading] = useState(false);
    const [user, setuser] = useState({
        prenom:"",
        postnom:"",
        phone:"",
        username:"",
        password:"",
        conpassword:""
    })

    const handleChange = e => {
        const { name, value } = e.target;
        setuser(prevState => ({
            ...prevState,
            [name]: value
        }));
};

const reset=()=>{
    setuser({
        prenom:"",
        postnom:"",
        phone:"",
        username:"",
        password:"",
        conpassword:""
    })
}

const onFinish=e=>{
    e.preventDefault();
    //console.log(user);
    setloading(true);
    usersServices.save(user).then(result=>{
        setloading(false);
        alert("Saved");
        reset();
    }).catch(error=>{
        setloading(false);
        alert("Error");
        console.log(error);
    })
    // dispatch(addBanque(banque));
    // setLarge(false);
}
    
    return (
        <div className="bg-gray-900 py-12 shandow rounded-lg mb-4 w-full flex flex-col items-center border-t-2 border-black">
            <h2 className="font-bold text-xl text-gray-400">Signup for more features</h2>
            <form className="max-w-xl w-full text-gray-200 mt-4 px-8 md:px-0" action="" method="POST" onSubmit={onFinish}>
                <div className="flex flex-col md:flex-row md:-mx-2">
                <label htmlFor="prenom" className="sr-only" >Prenom</label>
                    <input type="text" name="prenom" className="bg-gray-700 mt-4 border border-gray-600 rounded-lg py-2 px-4 w-full md:mx-2" placeholder="Prenom" id="prenom" required onChange={handleChange} value={user.prenom}/>
               
                    <label htmlFor="postnom" className="sr-only" >Postnom</label>
                    <input type="text" name="postnom" className="bg-gray-700 mt-4 border border-gray-600 rounded-lg py-2 px-4 w-full md:mx-2" placeholder="Postnom" id="postnom" required onChange={handleChange} value={user.postnom}/>
                </div>
                <div className="flex flex-col md:flex-row md:-mx-2">
                    <label htmlFor="phone" className="sr-only" >Phone</label>
                    <input type="text" name="phone" className="bg-gray-700 mt-4 border border-gray-600 rounded-lg py-2 px-4 w-full md:mx-2" placeholder="Telephone" id="phone" required onChange={handleChange} value={user.phone}/>
                    <label htmlFor="email" className="sr-only" >Email (username)</label>
                    <input type="email" name="username" className="bg-gray-700 mt-4 border border-gray-600 rounded-lg py-2 px-4 w-full md:mx-2" placeholder="Email" id="email" required onChange={handleChange} value={user.username}/>
                </div>
                <div className="flex flex-col md:flex-row md:-mx-2">
                    
                <label htmlFor="password" className="sr-only" >Password</label>
                    <input type="password" name="password" className="bg-gray-700 mt-4 border border-gray-600 rounded-lg py-2 px-4 w-full md:mx-2" placeholder="Password" id="password" required onChange={handleChange} value={user.password}/>
                    <label htmlFor="conpassword" className="sr-only" >Confirm password</label>
                    <input type="password" name="conpassword" className="bg-gray-700 mt-4 border border-gray-600 rounded-lg py-2 px-4 w-full md:mx-2" placeholder="Confirm Password" id="conpassword" required onChange={handleChange} value={user.conpassword}/>
                </div>
                <div className="flex flex-col md:flex-row md:justify-center md:items-center">
                    <button className="bg-green-900 text-sm mt-8 rounded-lg py-2 px-4 md:w-2/3 w-full">Signup</button>
                </div>
            </form>
            <FullPageLoader loading={loading}/>
        </div>
       
    )
}
