import Axios from "axios";
import { apiUrl,authHeader} from "../../_helps";
export const usersServices = {
    save,login
}
function save(data) {
    const requestOptions = {
        headers: authHeader()
    };
    return Axios.post(`${apiUrl()}/users/`,data,requestOptions)
        .then((response) => {
            return response;
        });
}

function login(username, password) {
    const user = {
        username,
        password
    };
    return Axios.post(`${apiUrl()}/login`, user)
        .then(handleResponse)
        .then((user) => {
            localStorage.setItem("user", JSON.stringify(user));
            return user;
        });
}

