import React,{useState} from 'react'
import FullPageLoader from "./_components/FullPageLoader/FullPageLoader";
import {usersServices} from "./_services";
import { withRouter } from 'react-router-dom';
function Login(props) {
    const [loading, setloading] = useState(false);
    const [user, setuser] = useState({
        email:"",
        password:"",
    })

    const handleChange = e => {
        const { name, value } = e.target;
        setuser(prevState => ({
            ...prevState,
            [name]: value
        }));
};

const reset = () => {
    setuser({
        email:"",
        password:"",
    });
};

const onFinish=e=>{
    e.preventDefault();
    //console.log(user);
    setloading(true);
    usersServices.login(user.email, user.password)
                  .then(() => {
                      setloading(false);
                      reset();
                      props.history.push("/");
                  }).catch((error) => {
                  setloading(false);
                  if(error.toString() === "Error: Request failed with status code 403"){
                    alert("Incorrect username or password !");
                  }else{
                    alert("Login : "+error);
                  }
                  console.log("Login error ",error);
            })
    // dispatch(addBanque(banque));
    
    // setLarge(false);
}
    return (
        <div className="bg-black w-full">
        <div className="bg-gray-900 py-12 shandow rounded-lg mb-4 w-full flex flex-col items-center border-t-2 border-black">
            <h2 className="font-bold text-xl text-gray-400">Sign in to go futher</h2>
            <form className="max-w-xl w-full text-gray-200 mt-4 px-8 md:px-0" action="" method="POST" onSubmit={onFinish}>
                <div className="flex flex-col md:flex-row md:-mx-2">
                    <label htmlFor="email" className="sr-only" >Email</label>
                    <input type="email" name="email" className="bg-gray-700 mt-4 border border-gray-600 rounded-lg py-2 px-4 w-full md:mx-2" placeholder="Email" id="email" onChange={handleChange} value={user.email} required/>
               
                    <label htmlFor="password" className="sr-only" >Password</label>
                    <input type="password" name="password" className="bg-gray-700 mt-4 border border-gray-600 rounded-lg py-2 px-4 w-full md:mx-2" placeholder="Password" id="password" onChange={handleChange} value={user.password} required/>
                </div>
                <div className="flex flex-col md:flex-row md:justify-center md:items-center">
                    <button className="bg-green-900 text-sm mt-8 rounded-lg py-2 px-4 md:w-2/3 w-full">Log in</button>
                </div>
                
            </form>
        </div>
        <FullPageLoader loading={loading}/>
        </div>
      
    )
}
export default withRouter(Login);
