import React from 'react';
import {Link,withRouter} from 'react-router-dom';
import {isAuth} from "../_helps";


function Dropdown({isOpen,history}) {
    const handleLogOut=()=>{
        localStorage.removeItem('user');
        history.push("/");
      }
    return (
        
        <div className={isOpen ? "bg-gray-300 max-w-2xl flex justify-center  text-center  text-gray-100 py-2 mt-14 sm:mt-0 " : 'hidden'}>
            <Link className="p-4 cursor-pointer  bg-gradient-to-r from-gray-700 to-gray-600 h-10 mx-2 shadow px-6 py-2 rounded-lg" to='/'>Home</Link>
            { isAuth() && (<Link className="p-4 cursor-pointer  bg-gradient-to-r from-gray-700 to-gray-600 h-10 mx-2 shadow px-6 py-2 rounded-lg" to="/security/signup">Signup</Link>)}
            { !isAuth() && ( <Link className="p-4 cursor-pointer bg-gradient-to-r from-gray-700 to-gray-600 h-10 mx-2 shadow px-6 py-2 rounded-lg" to='/security/login'>Login</Link>)}
            { isAuth() && (<button className="p-4 cursor-pointer bg-gradient-to-r from-gray-700 to-gray-600 h-10 mx-2 shadow px-6 py-2 rounded-lg" onClick={handleLogOut}>Logout</button>)}
        </div>
    )
}
export default withRouter(Dropdown);
